import { Box, Flex, Text, Link } from "@chakra-ui/react";

export default function Navbar() {
  return (
    <Flex wrap="wrap" alignItems="center" justifyContent="space-between" mb="10px" boxShadow="0 2px 2px -2px gainsboro" position="sticky" top="0" background="white">
      <Box>
        <Text as="b" fontSize="35px" fontWeight="extrabold" align="left" color="brand.themeColor">
          PORTFOLIO
        </Text>
      </Box>
      <Box w="40%" display="flex" justifyContent="space-around">
        <Link as="b" textUnderlineOffset="0.5em">
          <a href="/">Home</a>
        </Link>
        <Link as="b" textUnderlineOffset="0.5em">
          <a href="#about">About</a>
        </Link>
        <Link as="b" textUnderlineOffset="0.5em">
          <a href="#f">Skills</a>
        </Link>
        <Link as="b" textUnderlineOffset="0.5em">
          <a href="#a">Projects</a>
        </Link>
        <Link as="b" textUnderlineOffset="0.5em">
          <a href="#footer">Contact</a>
        </Link>
        <Link as="b" textUnderlineOffset="0.5em">
          <a href="#footer">Resume</a>
        </Link>
      </Box>
    </Flex>
  );
}
